<template>
<div id="setting">
  <HeaderNav :title="msg"></HeaderNav>
  <div class="list">
    <div class="item flex-row jusB" @click="toItem(index)"  v-for="(item, index) in list" :key="index">
      <div class="name">{{item.name}}</div>
      <div class="arrowEl"></div>
    </div>
    <div  class="outS text-center f30" v-if="this.commonState.login_bol" @click="outIn">退出登录</div>
    <!-- 退出登录弹框 -->
    <prompt-box
      :dropBol="dropBol"
      @leftBtn="dropBol=false"
      @rightBtn="dropFn"
      rightTxt="确定"
      contentTxt="是否退出登录"></prompt-box>
  </div>
  <prompt-box
    :dropBol="isClear"
    @leftBtn="isClear=false"
    @rightBtn="confirmBtn"
    rightTxt="确定"
    contentTxt="是否清除缓存"
  >
  </prompt-box>
  <!--<prompt-box-->
    <!--:dropBol="isExit"-->
    <!--@leftBtn="isExit=false"-->
    <!--@rightBtn="exitLogin"-->
    <!--rightTxt="确定"-->
    <!--contentTxt="是否退出登录"-->
  <!--&gt;-->
  <!--</prompt-box>-->
</div>
</template>
<script>
import HeaderNav from '@/components/common/header-nav'
import PromptBox from '../../common/prompt-box'
import {mapState, mapMutations} from 'vuex'
export default {
  components: {
    HeaderNav,
    PromptBox
  },
  computed: mapState({
    commonState: state => state.common
  }),
  data () {
    return {
      msg: '设置',
      list: [
        {
          name: '清除缓存',
          url: ''
        },
        {
          name: '关于我们',
          url: '/AboutUs'
        }
        // {
        //   name: '接口调试',
        //   url: '/debug'
        // }
      ],
      dropBol: false,     // 退出登录弹框
      isClear: false // 是否清除缓存
    }
  },
  methods: {
    ...mapMutations([
      'common_login_bol',
      'common_login_box',
      'common_index_column',
      'common_set_userInfo',
      'common_get_channel',
      'common_get_recondChannel',
      'login_bol'
    ]),
    // 跳转或点击操作
    toItem (index) {
      let temp = this.list[index]
      if (temp.url) {
        this.$router.push(temp.url)
      } else if (temp.name === '清除缓存') {
        this.isClear = true
      } else if (temp.name === '退出') {
        this.isExit = true
      }
    },
    // 清除缓存
    confirmBtn () {
      localStorage.removeItem('domain')
      this.$utils.cache('about', null)
      this.$utils.cache('articleReward', null)
      this.$utils.cache('currentSmallVideo', null)
      this.$utils.cache('isFirst', null)
      this.$utils.cache('dataList', null)
      this.$utils.cache('debug', null)
      this.$utils.cache('everyTime', null)
      this.$utils.cache('showBigGifts', null)
      this.$utils.cache('firstSHOW', null)
      this.$utils.cache('first_login', null)
      this.$utils.cache('first_tips', null)
      this.$utils.cache('historys', null)
      this.$utils.cache('my_channel', null)
      this.$utils.cache('recomd_channel', null)
      this.$utils.cache('redTime', null)
      this.$utils.cache('shareCarInfo', null)
      this.$utils.cache('showNetWorkTip', null)
      this.$utils.cache('smallVideoList', null)
      this.$utils.cache('smllVideoIndex', null)
      this.$utils.cache('taskScroll', null)
      this.$utils.cache('showConfirm', null)
      this.$utils.cache('threeCount', null)
      this.$utils.cache('showGuild', null)
      this.$utils.cache('rewardId', null)
      this.$utils.cache('read_count', null)
      this.$utils.cache('videoList', null)
      this.$utils.cache('personalHeight', null)
      this.$utils.cache('currentDetail', null)
      this.$utils.cache('imageList', null)
      this.$utils.cache('isTop', null)
      this.$utils.cache('shareSuccess', null)
      this.$utils.cache('handOperated', null)
      this.$utils.cache('messageShow', null)
      this.$utils.cache('isFirstRead', null)
      this.$utils.cache('menu', null)
      this.$utils.cache('domain', null)
      this.$utils.cache('movieGuild', null)
      this.$utils.cache('playCache', null)
      this.$utils.cache('videoPage', null)
      this.$utils.cache('swipeGuild', null) // 滑动
      this.$utils.cache('shortLink', null)  // 短链接
      this.$utils.cache('arcTop', null)
      this.$utils.cache('hasAddBack', null)
      this.$store.commit('common_set_imageList', {list: []})
      this.isClear = false
      this.$toast({
        message: '清除缓存成功',
        duration: 1500
      })
    },
    // 确定退出
    dropFn () {
      this.dropBol = false
      this.common_login_bol(false)
      let data = {}
      if (this.commonState.pushInfo && this.commonState.pushInfo.clientid) {
        data.getui_cid = this.commonState.pushInfo.clientid
      }
      this.$api.get('LOGOUT', data, () => {
        this.$router.replace('/personal_center')
        this.$utils.cache('personalHeight', null)
        this.$toast({
          message: '退出成功',
          duration: 1500
        })
        this.$utils.cache('handOperated', 1)
        this.isExit = false
        this.common_login_bol(false)
        this.common_set_userInfo({})
        this.common_index_column(true)
        this.$utils.cache('threeCount', null)
        this.userInfo = {}
        this.user_icon = ''
        this.$utils.clearCache()
      })
    },
    outIn () {
      this.dropBol = true
    }
  }
}
</script>
<style lang="scss" scoped>
#setting {
  height: 100vh;
  background: #f2f2f2;
  .list {
    .item {
      padding: 0 32px;
      background: #fff;
      margin-bottom: 15px;
      height: 98px;
    }
  }
  .outS {
    background: #FF5757;
    border-radius: 10px;
    margin: 0 30px;
    margin-top: 90px;
    height: 90px;
    line-height: 90px;
    color: white;
  }
}
</style>



